.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
}

a {
  text-decoration: none;
}

.group {
  margin: 10px 0;
  width: 100%;
  position: relative;
}

.length-badge {
  top: 9px;
  right: 10px;
  position: absolute;
}

input {
  border-radius: 10%;
}

.sticky-thead {
  top: 0;
  position: sticky;
}

.container h1 {
  padding: 5px 10px;
  margin: 10px 0;
  border-radius: 15px;
}

.container h4 {
  margin: 3px 0;
}

.container span {
  font-size: 12px;
}

.container table {
  margin-top: 10px;
}